(function ()
{
    'use strict';
	
   angular
        .module('app.core')
        .directive('msTouchItemClick', directive);

    /** @ngInject */
    function directive($window)
    {
        return {
            restrict: 'A',
            link: function(scope, element, attr) {
                element.on('click', function(event) {
                    if($('html').hasClass('is-touch')){
                        if(event.type=='click'){
                            var item = element.closest('.touch-item');
                            if(item.length){
                                if(item.hasClass('active-touch')){
                                    return true;
                                }else{
                                    event.stopPropagation();
                                    event.preventDefault();
                                    event.stopNextHandler=true;
                                    deactiveItemsInParentTouchItemScope(item);
                                    item.addClass('active-touch');
                                    return false;
                                }
                            }
                        }
                    }
                });
            }
        };
    }

    var deactiveItemsInParentTouchItemScope = function(el){
        if($('html').hasClass('is-touch')){
            $(el).closest('.touch-item').find('.active-touch').removeClass('active-touch');
        }
    };

})();
(function ()
{
    'use strict';

    angular
        .module('app.core')
        .directive('msSplashScreen', msSplashScreenDirective);

    /** @ngInject */
    function msSplashScreenDirective($animate)
    {
        return {
            restrict: 'E',
            link    : function (scope, iElement)
            {
                var splashScreenRemoveEvent = scope.$on('msSplashScreen::remove', function ()
                {
                    $animate.leave(iElement).then(function ()
                    {
                        // De-register scope event
                        splashScreenRemoveEvent();

                        // Null-ify everything else
                        scope = iElement = null;
                    });
                });
            }
        };
    }
})();

