(function ()
{
    'use strict';

    angular
        .module('app.wishlist')
        .controller('WishListController', controller);

   /** @ngInject */
    function controller($scope, $interval, $translate, store, $sce, msApi, $state, $stateParams)
    {
       var self = this;
       this.$scope = $scope;
       this.$interval = $interval;
       this.$translate = $translate;
       this.artists = store.get('wishlist');
       this.store = store;
       this.msApi = msApi;
       this.$state = $state;
       this.$stateParams = $stateParams;
       this.$sce =  $sce;

        this.imagePopover = {
           isActive  : false,
           requestId : null,
           images : [],
           artist : null
       }
       this.imagePopOverArray = [];
       this.msApi = msApi;
   
    }
    controller.prototype.destroyImagePopover = function(){
        var self = this;
        self.imagePopover.isActive = false;
    }

    controller.prototype.initImagePopover = function(event, artist){
        var self =  this;
        var requestId = artist.artist_id;
        if(requestId != self.imagePopover.requestId){
            self.imagePopover.images = self.imagesPopOverArray;
        }
        artist.infos = $.map(artist.artist_information, function(item, key){
            return {
                key : key,
                value : item
            }
        });
        self.imagePopover.requestId =requestId;
        self.imagePopover.isActive = true;
        self.imagePopover.artist  = artist;
        self.msApi.request('artists.files@query', {type : 'images_artist', 'artist_id' : artist.artist_id, page_size : 4}, function(resp){
             self.imagePopover.images = resp.items;
        }, function(err){

        });
	};
     controller.prototype.toogleWishList = function(event, artist){
        if(event){
            event.preventDefault();
            event.stopImmediatePropagation();
        }
        var self = this;
        var list = self.store.get('wishlist') == null ? [] :  self.store.get('wishlist');
        var indexData = -1;
        $.each(list, function(index ,item){
            if(artist.artist_id === item.artist_id){
                indexData = index;
                return false;
            }
        });
        if(indexData > -1){
            list.splice(indexData, 1);
            $('#model-image-popover').hide();
        }else{
            list.push(artist);
        }
        self.store.set('wishlist', list);
        return false;
		
	}

    controller.prototype.existed = function(artist){
        var self = this;
        var list = self.store.get('wishlist');
        var indexData = -1;
        $.each(list, function(index ,item){
            if(artist.artist_id === item.artist_id){
                indexData = index;
                return false;
            }
        });
        return indexData > -1;
	}


})();

