(function ()
{
    'use strict';

    angular
        .module('app.artist')
        .controller('ArtistController', controller);

   /** @ngInject */
    function controller($scope, $interval, $translate, store, $sce, Artist, msApi, $state, Images, $stateParams, Videos)
    {
       var self = this;
       this.$scope = $scope;
       this.$interval = $interval;
       this.$translate = $translate;
       this.user_profile = store.get('user');
      
       this.artist = Artist;
       this.images = Images.items;
       this.videos = Videos.items;
       this.artist.infos = $.map(this.artist.artist_information, function(item, key){
            return {
                key : key,
                value : item
            }
       });
       this.msApi = msApi;
       this.$state = $state;
       this.$stateParams = $stateParams;
       this.$sce =  $sce;
       this.pagination = {
            currentPage: 1,
            isBusy:false,
            isMoreAvailable: self.images.length < Images.total_items,
            load:function(){
                self.loadImageFiles();
            },
            reset:function(){
                $scope.pagination.currentPage=1;
                $scope.pagination.isBusy = false;
                $scope.pagination.isMoreAvailable = true;
                $scope.artists = [];
            }
		}
		
    }

    controller.prototype.backToArtists = function(event){
        if(event){
            event.preventDefault();
        }
        this.$state.go('app.artists');
    }
    
    controller.prototype.loadImageFiles = function(page){
        var self = this;
        if(self.pagination.isMoreAvailable && !self.pagination.isBusy){
            self.pagination.isBusy = true;
            
            var targetPage = self.pagination.currentPage + 1;
        
            self.msApi.request('artists.files@query', {page_size : 20, page : targetPage, type : 'images_artist', 'artist_id' : self.$stateParams.slug}, function(resp){
                    self.images = self.images.concat(resp.items);
                    self.pagination.isBusy = false;
                    self.pagination.currentPage = targetPage;
                    if(self.images.length >= resp.total_items){
                        self.pagination.isMoreAvailable = false;
                    }
            }, function(err){
                    self.pagination.isBusy = false;
            });
            
            
        }
	}

    controller.prototype.trustSrc = function(src) {
        return this.$sce.trustAsResourceUrl(src);
    }
    
    controller.prototype.embedVideo = function($event){
        var el =  angular.element($event.currentTarget);
        el.fadeOut();
        
        var target = $('#'+el.attr('data-target'));
        var src = el.attr('data-src');
        if (src.indexOf("?") >= 0){
            src+="&";
        } else{
            src+="?";
        }
        src +="autoplay=1";
        src = src.replace("watch?v=", "v/")
        target.replaceWith('<iframe  src="'+src+'" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>');
    }

})();

